export default {
  props: {
    title: {
      type: String,
      required: false,
      default: ''
    },
    imgurl: {
      type: String,
      required: false,
      default: ''
    },
    cost: {
      type: Number,
      required: false,
      default: 0
    },
    cost1: {
      type: Number,
      required: false,
      default: 0
    },
    stockList:null,
    isMember:null
  },
  data () {
    return {
    normalMax:'',
    normalMin:'',
    memberMax:'',
    memberMin:'',
    normal:[],
    member:[],
    isequal:false
    }
  },
  created(){
    this.compare();
  },
  methods:{
       compareNormal(a){
    let min = a[0]; 
let max =a[0];
　　　　　　for(let i = 0; i < a.length; i++){
　　　　　　　　let cur = a[i];
　　　　　　　　cur > max ? max = cur : null;
　　　　　　　　cur < min ? min = cur : null;　　
　　　　　　}
 this.normalMax=max;
 this.normalMin=min;
     },
    compareMember(a){
let min = a[0]; 
let max =a[0];
　　　　　　for(let i = 0; i < a.length; i++){
　　　　　　　　let cur = a[i];
　　　　　　　　cur > max ? max = cur : null;
　　　　　　　　cur < min ? min = cur : null;　　
　　　　　　}
 this.memberMax=max;
 this.memberMin=min;
    },
    compare(){
      if(this.stockList.length>1){
         let normal=[];
         let member=[];
            this.stockList.forEach(val=>{ //价格区间
          this.normal.push(val.salePrice)
          this.member.push(val.memberPrice)
        })
         this.compareMember(this.member);
         this.compareNormal(this.normal)
          if(this.normalMax==this.normalMin){
            this.isequal=true;
          }
        }else{
           this.isequal=true;
        }
    }
  }
}



